// utils/excelProcessor.js
import * as XLSX from 'xlsx';

// Excel文件处理器 - 专注于解决Vue项目中Excel加载问题
export const processJobData = async () => {
  try {
    console.log('开始处理Excel职位数据...');
    
    // 1. 首先尝试从public目录加载Excel文件
    // 这是最可靠的方法，确保在开发和生产环境都能正常工作
    const publicPaths = [
      'datasets/JobWanted.xlsx',
      'assets/datasets/JobWanted.xlsx',
      'excel/JobWanted.xlsx',
      'JobWanted.xlsx'
    ];
    
    let fileData = null;
    let successPath = '';
    
    // 尝试从public目录读取文件
    for (const path of publicPaths) {
      try {
        console.log(`尝试从public目录加载: ${path}`);
        const response = await fetch(path, {
          method: 'GET',
          // 确保请求Excel文件类型
          headers: {
            'Accept': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
            'Cache-Control': 'no-cache'
          }
        });
        
        if (!response.ok) {
          console.warn(`路径${path}请求失败: ${response.status}`);
          continue;
        }
        
        const arrayBuffer = await response.arrayBuffer();
        fileData = new Uint8Array(arrayBuffer);
        successPath = path;
        console.log(`成功从${path}加载Excel文件，大小: ${fileData.length} 字节`);
        break;
      } catch (error) {
        console.warn(`从${path}加载失败:`, error);
      }
    }
    
    // 如果无法从public目录加载，提供清晰的错误信息
    if (!fileData) {
      console.error('无法从public目录加载Excel文件');
      
      // 提供解决方案指导
      const errorMessage = `
        无法找到Excel文件。请确保您已将JobWanted.xlsx文件放置在public目录中。
        
        正确的做法是:
        1. 在项目根目录下找到public文件夹
        2. 在public文件夹中创建datasets目录
        3. 将Excel文件复制到public/datasets/JobWanted.xlsx
        4. 重启开发服务器
        
        问题原因:
        - Vue项目中，只有public目录下的文件可在生产环境中被可靠访问
        - src/assets目录下的文件会被webpack处理，不能直接通过URL访问
      `;
      
      throw new Error(errorMessage);
    }
    
    // 2. 使用XLSX库解析Excel数据
    try {
      console.log('开始解析Excel数据...');
      
      const workbook = XLSX.read(fileData, {
        type: 'array',
        cellDates: true,
        cellStyles: true,
      });
      
      console.log(`Excel工作簿有${workbook.SheetNames.length}个工作表`);
      console.log('工作表名称:', workbook.SheetNames);
      
      // 获取第一个工作表
      const worksheet = workbook.Sheets[workbook.SheetNames[0]];
      
      // 确保工作表有数据
      if (!worksheet || !worksheet['!ref']) {
        throw new Error('Excel工作表为空或没有数据范围');
      }
      
      // 将Excel转换为JSON
      const jsonData = XLSX.utils.sheet_to_json(worksheet, {
        raw: false,
        defval: ''
      });
      
      if (!jsonData || jsonData.length === 0) {
        throw new Error('Excel数据解析结果为空');
      }
      
      console.log(`成功解析Excel数据，共${jsonData.length}条记录`);
      console.log('数据示例:', jsonData[0]);
      
      // 3. 分析处理数据
      return analyzeJobData(jsonData);
    } catch (parseError) {
      console.error('解析Excel失败:', parseError);
      throw new Error(`解析Excel文件错误: ${parseError.message}。请确认文件格式正确。`);
    }
  } catch (error) {
    console.error('Excel数据处理失败:', error);
    throw error;
  }
};

// 计算平均薪资 - 确保这个函数在generateJobProfiles之前定义
const calculateAvgSalary = (salaries) => {
  if (!salaries || !salaries.length) return 0;
  
  let total = 0;
  let count = 0;
  
  salaries.forEach(salary => {
    const salaryStr = String(salary);
    const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
    if (match) {
      const min = parseInt(match[1]);
      const max = match[2] ? parseInt(match[2]) : min;
      total += (min + max) / 2;
      count++;
    }
  });
  
  return count > 0 ? (total / count).toFixed(1) : 0;
};

// 辅助函数：寻找匹配的字段名
const findFieldName = (obj, possibleNames) => {
  for (const name of possibleNames) {
    if (obj[name] !== undefined) {
      return name;
    }
  }
  
  // 尝试忽略大小写比较
  const keys = Object.keys(obj);
  for (const name of possibleNames) {
    const lowerName = name.toLowerCase();
    const match = keys.find(key => key.toLowerCase() === lowerName);
    if (match) {
      return match;
    }
  }
  
  return null;
};

// 通用字段分析函数
const analyzeByField = (data, field) => {
  const counts = {};
  data.forEach(item => {
    const value = item[field];
    if (value && value.trim && value.trim() !== '') { // 确保value是字符串并且不为空
      counts[value] = (counts[value] || 0) + 1;
    }
  });
  
  return Object.entries(counts)
    .map(([name, value]) => ({ name, value }))
    .sort((a, b) => b.value - a.value);
};

// 薪资分析函数
const analyzeSalary = (data) => {
  const salaryRanges = {
    '0-5K': 0,
    '5-10K': 0,
    '10-15K': 0,
    '15-20K': 0,
    '20K+': 0
  };
  
  data.forEach(item => {
    const salary = item.salary;
    if (!salary || (salary.trim && salary.trim() === '')) return;
    
    // 解析各种薪资格式
    const salaryStr = String(salary); // 确保是字符串
    const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
    
    if (match) {
      const min = parseInt(match[1]);
      const max = match[2] ? parseInt(match[2]) : min;
      const avg = (min + max) / 2;
      
      if (avg < 5) salaryRanges['0-5K']++;
      else if (avg < 10) salaryRanges['5-10K']++;
      else if (avg < 15) salaryRanges['10-15K']++;
      else if (avg < 20) salaryRanges['15-20K']++;
      else salaryRanges['20K+']++;
    }
  });
  
  return Object.entries(salaryRanges)
    .map(([name, value]) => ({ name, value }))
    .filter(item => item.value > 0); // 只返回有值的区间
};

// 首先修改dataProcessor.js中的generateJobProfiles函数，确保返回完整的薪资数据

// 修改generateJobProfiles函数，在jobProfiles中包含完整的salaries数组
const generateJobProfiles = (data) => {
  // 按职位类型分组
  const jobGroups = {};
  
  data.forEach(item => {
    const jobTitle = item.job_title;
    if (!jobTitle || (jobTitle.trim && jobTitle.trim() === '')) return; // 验证数据
    
    if (!jobGroups[jobTitle]) {
      jobGroups[jobTitle] = {
        title: jobTitle,
        count: 0,
        cities: {},
        salaries: [],  // 存储所有薪资数据
        citySalaries: {}, // 按城市存储薪资
        companies: new Set(),
        industries: {},
        experiences: {}, 
        educations: {}
      };
    }
    
    const group = jobGroups[jobTitle];
    group.count++;
    
    // 城市统计
    const city = item.city || '';
    if (city && city.trim && city.trim() !== '') {
      // 更新城市计数
      group.cities[city] = (group.cities[city] || 0) + 1;
      
      // 初始化城市薪资数组（如果不存在）
      if (!group.citySalaries[city]) {
        group.citySalaries[city] = [];
      }
    }
    
    // 薪资收集 - 确保薪资数据正确收集
    const salary = item.salary || '';
    if (salary && salary !== '') {
      // 添加到总体薪资列表
      group.salaries.push(String(salary));
      
      // 如果有城市信息，也添加到城市薪资列表
      if (city && city.trim && city.trim() !== '') {
        group.citySalaries[city].push(String(salary));
      }
    }
    
    // 公司和行业
    if (item.company && item.company.trim && item.company.trim() !== '') {
      group.companies.add(item.company);
    }
    
    if (item.company_type && item.company_type.trim && item.company_type.trim() !== '') {
      group.industries[item.company_type] = (group.industries[item.company_type] || 0) + 1;
    }
    
    // 添加经验统计
    if (item.experience && item.experience.trim && item.experience.trim() !== '') {
      group.experiences[item.experience] = (group.experiences[item.experience] || 0) + 1;
    }
    
    // 添加学历统计
    if (item.education && item.education.trim && item.education.trim() !== '') {
      group.educations[item.education] = (group.educations[item.education] || 0) + 1;
    }
  });
  
  // 生成职位画像
  return Object.values(jobGroups)
    .sort((a, b) => b.count - a.count)
    .slice(0, 10) // 取前10个热门职位
    .map(group => ({
      title: group.title,
      count: group.count,
      topCities: Object.entries(group.cities)
        .sort((a, b) => b[1] - a[1])
        .slice(0, 5)
        .map(([city, count]) => ({ city, count })),
      avgSalary: calculateAvgSalary(group.salaries),
      // 确保返回完整的薪资数据
      salaries: group.salaries,
      // 按城市分组的薪资数据
      citySalaries: group.citySalaries,
      // 所有城市列表
      allCities: Object.keys(group.cities),
      companyCount: group.companies.size,
      topIndustries: Object.entries(group.industries)
        .sort((a, b) => b[1] - a[1])
        .slice(0, 3)
        .map(([industry, count]) => ({ industry, count })),
      // 添加经验要求返回
      topExperiences: Object.entries(group.experiences)
        .sort((a, b) => b[1] - a[1])
        .slice(0, 3)
        .map(([exp, count]) => ({ exp, count })),
      // 添加学历要求返回
      topEducations: Object.entries(group.educations)
        .sort((a, b) => b[1] - a[1])
        .slice(0, 3)
        .map(([edu, count]) => ({ edu, count }))
    }));
};

const generateSalaryDetailData = (data) => {
  // 计算总职位数
  const totalJobs = data.length;
  
  // 分析高薪职位和低薪职位
  const salaryGroups = {
    high: [], // 15K以上
    low: []   // 8K以下
  };
  
  data.forEach(item => {
    const salary = item.salary;
    if (!salary || (salary.trim && salary.trim() === '')) return;
    
    // 解析薪资
    const salaryStr = String(salary);
    const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
    
    if (match) {
      const min = parseInt(match[1]);
      const max = match[2] ? parseInt(match[2]) : min;
      const avg = (min + max) / 2;
      
      // 归类为高薪或低薪
      if (avg >= 15) {
        salaryGroups.high.push(item);
      } else if (avg <= 8) {
        salaryGroups.low.push(item);
      }
    }
  });
  
  // 高薪职位详细分布
  const highSalaryJobs = salaryGroups.high.length;
  
  // 分析高薪职位的薪资区间分布
  const highSalaryDistribution = {
    '30K以上': 0,
    '20-30K': 0,
    '15-20K': 0,
    '其他': 0
  };
  
  salaryGroups.high.forEach(item => {
    const salaryStr = String(item.salary);
    const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
    
    if (match) {
      const min = parseInt(match[1]);
      const max = match[2] ? parseInt(match[2]) : min;
      const avg = (min + max) / 2;
      
      if (avg >= 30) {
        highSalaryDistribution['30K以上']++;
      } else if (avg >= 20) {
        highSalaryDistribution['20-30K']++;
      } else if (avg >= 15) {
        highSalaryDistribution['15-20K']++;
      } else {
        highSalaryDistribution['其他']++;
      }
    }
  });
  
  // 低薪职位详细分布
  const lowSalaryJobs = salaryGroups.low.length;
  
  // 分析低薪职位的薪资区间分布
  const lowSalaryDistribution = {
    '5K以下': 0,
    '5-8K': 0,
    '8-10K': 0,
    '其他': 0
  };
  
  salaryGroups.low.forEach(item => {
    const salaryStr = String(item.salary);
    const match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)?(?:k|K)?/i);
    
    if (match) {
      const min = parseInt(match[1]);
      const max = match[2] ? parseInt(match[2]) : min;
      const avg = (min + max) / 2;
      
      if (avg < 5) {
        lowSalaryDistribution['5K以下']++;
      } else if (avg < 8) {
        lowSalaryDistribution['5-8K']++;
      } else if (avg < 10) {
        lowSalaryDistribution['8-10K']++;
      } else {
        lowSalaryDistribution['其他']++;
      }
    }
  });
  
  // 转换为chart6所需的数据格式
  const highSalaryDetail = {
    title: '高薪职位分布',
    data: [
      { value: highSalaryJobs, name: '高薪职位', itemStyle: { color: '#c0232a' } },
      { value: totalJobs - highSalaryJobs, name: '其他职位', itemStyle: { color: '#2870e8' } },
    ],
    data1: [
      { value: highSalaryDistribution['30K以上'], name: '30K以上', itemStyle: { color: '#c0232a' } },
      { value: highSalaryDistribution['20-30K'], name: '20-30K', itemStyle: { color: '#2870e8' } },
      { value: highSalaryDistribution['15-20K'], name: '15-20K', itemStyle: { color: '#feed2c' } },
      { value: highSalaryDistribution['其他'], name: '其他', itemStyle: { color: '#a262f2' } }
    ]
  };
  
  const lowSalaryDetail = {
    title: '低薪职位分布',
    data: [
      { value: lowSalaryJobs, name: '低薪职位', itemStyle: { color: '#c0232a' } },
      { value: totalJobs - lowSalaryJobs, name: '其他职位', itemStyle: { color: '#2870e8' } },
    ],
    data1: [
      { value: lowSalaryDistribution['5K以下'], name: '5K以下', itemStyle: { color: '#c2232a' } },
      { value: lowSalaryDistribution['5-8K'], name: '5-8K', itemStyle: { color: '#fe672c' } },
      { value: lowSalaryDistribution['8-10K'], name: '8-10K', itemStyle: { color: '#a262f2' } },
      { value: lowSalaryDistribution['其他'], name: '其他', itemStyle: { color: '#2870e8' } }
    ]
  };
  
  return {
    highSalaryDetail,
    lowSalaryDetail
  };
};


// 添加到dataProcessor.js中，增强薪资分析功能

// 增强版的薪资分析函数，用于生成箱线图数据
const generateSalaryBoxplotData = (data) => {
  // 收集所有职位的薪资数据
  const jobSalaryData = {};
  
  // 按职位和城市分组薪资数据
  data.forEach(item => {
    const jobTitle = item.job_title;
    const city = item.city;
    const salary = item.salary;
    
    if (!jobTitle || !salary) return;
    
    // 初始化职位数据
    if (!jobSalaryData[jobTitle]) {
      jobSalaryData[jobTitle] = {
        allSalaries: [],
        citySalaries: {}
      };
    }
    
    // 添加薪资到总体列表
    jobSalaryData[jobTitle].allSalaries.push(salary);
    
    // 如果有城市信息，按城市分组
    if (city) {
      if (!jobSalaryData[jobTitle].citySalaries[city]) {
        jobSalaryData[jobTitle].citySalaries[city] = [];
      }
      jobSalaryData[jobTitle].citySalaries[city].push(salary);
    }
  });
  
  // 获取排名前10的职位
  const topJobTitles = Object.keys(jobSalaryData)
    .sort((a, b) => jobSalaryData[b].allSalaries.length - jobSalaryData[a].allSalaries.length)
    .slice(0, 10);
  
  // 计算所有职位的箱线图数据
  const boxplotData = topJobTitles.map(jobTitle => {
    const salaries = jobSalaryData[jobTitle].allSalaries;
    return calculateSalaryBoxplot(salaries);
  });
  
  // 计算城市薪资差异数据
  // 选择排名前3的职位，比较一线城市、二线城市和三线城市的薪资差异
  const cityComparisonData = {};
  const topCities = ['北京', '上海', '广州', '深圳', '杭州', '成都'];
  
  topJobTitles.slice(0, 3).forEach(jobTitle => {
    const jobData = jobSalaryData[jobTitle];
    
    cityComparisonData[jobTitle] = {
      title: jobTitle,
      cityData: {}
    };
    
    // 遍历城市数据
    Object.keys(jobData.citySalaries).forEach(city => {
      if (topCities.includes(city)) {
        const citySalaries = jobData.citySalaries[city];
        cityComparisonData[jobTitle].cityData[city] = calculateSalaryBoxplot(citySalaries);
      }
    });
  });
  
  return {
    jobTitles: topJobTitles,
    boxplotData: boxplotData,
    cityComparisonData: cityComparisonData
  };
};

// 从薪资字符串数组计算箱线图数据 [min, Q1, median, Q3, max]
const calculateSalaryBoxplot = (salaries) => {
  if (!salaries || salaries.length === 0) return [0, 0, 0, 0, 0];
  
  // 解析薪资字符串为数值
  const salaryValues = [];
  
  salaries.forEach(salary => {
    const salaryStr = String(salary);
    
    // 匹配区间格式: "10K-15K"
    let match = salaryStr.match(/(\d+)(?:k|K)?(?:\s*[-~]\s*)(\d+)(?:k|K)?/i);
    
    if (match) {
      const min = parseInt(match[1]);
      const max = parseInt(match[2]);
      const avg = (min + max) / 2;
      salaryValues.push(avg);
      return;
    }
    
    // 匹配单值格式: "15K"
    match = salaryStr.match(/^(\d+)(?:k|K)$/i);
    
    if (match) {
      const value = parseInt(match[1]);
      salaryValues.push(value);
      return;
    }
    
    // 匹配纯数字: "15"
    match = salaryStr.match(/^(\d+)$/);
    
    if (match) {
      const value = parseInt(match[1]);
      salaryValues.push(value);
    }
  });
  
  if (salaryValues.length === 0) return [0, 0, 0, 0, 0];
  
  // 排序薪资值
  salaryValues.sort((a, b) => a - b);
  
  const min = salaryValues[0];
  const max = salaryValues[salaryValues.length - 1];
  
  // 如果只有一个值，所有分位数都等于该值
  if (salaryValues.length === 1) {
    return [min, min, min, min, max];
  }
  
  // 计算四分位数
  const q1Index = Math.floor(salaryValues.length * 0.25);
  const medianIndex = Math.floor(salaryValues.length * 0.5);
  const q3Index = Math.floor(salaryValues.length * 0.75);
  
  const q1 = salaryValues[q1Index];
  const median = salaryValues[medianIndex];
  const q3 = salaryValues[q3Index];
  
  return [min, q1, median, q3, max];
};

const analyzeJobData = (data) => {
  // 确保数据是数组且不为空
  if (!Array.isArray(data) || data.length === 0) {
    throw new Error('无效的数据格式，预期为非空数组');
  }
  
  // 检查数据结构，自动映射字段
  console.log('数据字段检测中...');
  const sampleItem = data[0];
  const fieldMap = {
    job_title: findFieldName(sampleItem, ['job_title', 'jobTitle', 'title', 'position', '职位', '职位名称']),
    city: findFieldName(sampleItem, ['city', 'location', '城市', '工作地点']),
    salary: findFieldName(sampleItem, ['salary', 'salaryRange', '薪资', '薪资范围']),
    experience: findFieldName(sampleItem, ['experience', 'workExperience', '经验', '工作经验']),
    education: findFieldName(sampleItem, ['education', 'eduBackground', '学历', '教育背景']),
    company: findFieldName(sampleItem, ['company', 'companyName', '公司', '公司名称']),
    company_type: findFieldName(sampleItem, ['company_type', 'companyType', 'industry', '公司类型', '行业'])
  };
  
  console.log('检测到字段映射:', fieldMap);
  
  // 标准化数据
  const normalizedData = data.map(item => {
    const normalized = {};
    Object.entries(fieldMap).forEach(([standardField, originalField]) => {
      if (originalField && item[originalField] !== undefined) {
        normalized[standardField] = item[originalField];
      } else {
        normalized[standardField] = ''; // 设置默认值
      }
    });
    return normalized;
  });
  
  console.log(`数据标准化完成，共处理${normalizedData.length}条记录`);
  
  // 1. 行业类别分析
  const industryAnalysis = analyzeByField(normalizedData, 'company_type');
  
  // 2. 薪资分析
  const salaryAnalysis = analyzeSalary(normalizedData);
  
  // 3. 经验要求分析
  const experienceAnalysis = analyzeByField(normalizedData, 'experience');
  
  // 4. 学历要求分析
  const educationAnalysis = analyzeByField(normalizedData, 'education');
  
  // 5. 城市分布分析
  const cityAnalysis = analyzeByField(normalizedData, 'city');
  
  // 6. 职位画像数据
  const jobProfiles = generateJobProfiles(normalizedData);
  
  // 7. 薪资详细分析数据 (高薪与低薪职位)
  const salaryDetailData = generateSalaryDetailData(normalizedData);
  
  // 8. 薪资箱线图数据
  const jobSalaryComparisonData = generateSalaryBoxplotData(normalizedData);
  
  // 9. 职位差异度分析数据
  const jobDifferenceData = generateJobDifferenceData(normalizedData, {
    salaryAnalysis,
    experienceAnalysis,
    educationAnalysis,
    industryAnalysis,
    cityAnalysis
  });
  
  return {
    industryAnalysis,
    salaryAnalysis,
    experienceAnalysis,
    educationAnalysis,
    cityAnalysis,
    jobProfiles,
    salaryDetailData,
    jobSalaryComparisonData,
    jobDifferenceData
  };
};

// 计算职位差异度数据
const generateJobDifferenceData = (data, analysisResults) => {
  const dimensions = ['薪资差异', '经验差异', '学历差异', '行业差异', '地域差异'];
  
  // 计算各维度的差异度分数
  // 使用熵值计算多样性/差异度
  const calcDiversity = (items) => {
    if (!items || !items.length) return 50; // 默认值
    
    // 计算总数
    const total = items.reduce((sum, item) => sum + item.value, 0);
    if (total === 0) return 50;
    
    // 计算每项的占比
    const proportions = items.map(item => item.value / total);
    
    // 计算熵值
    const entropy = proportions.reduce((sum, p) => {
      if (p === 0) return sum;
      return sum - p * Math.log(p);
    }, 0);
    
    // 归一化到0-100
    const maxEntropy = Math.log(items.length);
    const normalizedDiversity = maxEntropy > 0 ? (entropy / maxEntropy) * 100 : 50;
    
    // 确保值在合理范围内
    return Math.min(100, Math.max(40, normalizedDiversity));
  };
  
  const salaryDiff = calcDiversity(analysisResults.salaryAnalysis);
  const expDiff = calcDiversity(analysisResults.experienceAnalysis);
  const eduDiff = calcDiversity(analysisResults.educationAnalysis);
  const indDiff = calcDiversity(analysisResults.industryAnalysis);
  const cityDiff = calcDiversity(analysisResults.cityAnalysis);
  
  return {
    dimensions,
    scores: [salaryDiff, expDiff, eduDiff, indDiff, cityDiff]
  };
};